.row {
  zoom: 1;
  max-width: 1140px;
  margin: 0;
}

.row:before,
.row:after {
  content: "";
  display: table;
}
.row:after {
  clear: both;
}

.col {
  display: block;
  float: left;
  margin: 1% 0 1% 0;
}

.col:first-child {
  margin-left: 0;
}
.span1of2 {
  width: 40%;
  /* background-color: burlywood; */
}
.span2of2 {
  width: 60%;
  /* background-color: red; */
}

@media only screen and (max-width: 480px) {
  .col {
    margin: 0;
  }

  /* .span3of3 {
		width: 100%;
	} */
}

/*  COMPONENT SETUP   ==================================================================== */

.total {
  width: 250px;
  height: 100px;
  /* background-color: red; */
}

.totalDiv {
  width: 100px;
  height: 100px;
  background-color: #116aab;
  /* background-color: white; */
  color: white;
  margin: 0 auto;
  border-radius: 100px;
  text-align: center;
  box-shadow: 2px 0 8px 2px rgba(0, 0, 0, 0.3);
  margin-top: 2%;
  cursor: pointer;
}
.valueTotal {
  font-size: 160%;
  font-weight: 800;
  padding-top: 20%;
}
.valueLabel {
  font-size: 70%;
  font-weight: 400;
}

.summaryProvDiv {
  width: 220px;
  /* background-color: #d9dcdf; */
  margin: 5% auto;
  border-radius: 5px;
  /* box-shadow: 2px 0 8px 2px rgba(0, 0, 0, 0.3); */
  padding: 2%;
  color: black;
}

.provLabel {
  font-size: 70%;
  font-weight: 400;
  padding-left: 10%;
  padding-top: 5%;
}

.provValue {
  font-size: 70%;
  font-weight: 700;
  padding-left: 80%;
}
.provDiv {
  margin-left: 5%;
  border-bottom: 1px solid rgb(196, 195, 195);
}
.provDiv:hover {
  background-color: #f7f7f7;
  cursor: pointer;
}
