.row {
  zoom: 1;
  max-width: 1140px;
  margin: 0;
}

.row:before,
.row:after {
  content: "";
  display: table;
}
.row:after {
  clear: both;
}

.col {
  display: block;
  float: left;
  margin: 1% 0 1% 0;
}

.col:first-child {
  margin-left: 0;
}
.span1of2 {
  width: 60%;
  /* background-color: burlywood; */
}
.span2of2 {
  width: 40%;
  /* background-color: red; */
}
/* .span3of3 {
	width: 33.31%;
	background-color: burlywood;
} */

@media only screen and (max-width: 480px) {
  .col {
    margin: 0;
  }

  /* .span3of3 {
		width: 100%;
	} */
}

/*  COMPONENT SETUP   ==================================================================== */
.panel {
  position: absolute;
  margin-top: 23.8%;
  z-index: 5;
  left: 0;
  cursor: pointer;

  /* width: 77.5%; */
}
.folderTop {
  background-color: #116aab;
  border-radius: 0 0 0 0;
  height: 30px;
}
.heading {
  font-size: 60%;
  color: white;
  font-weight: 700;
  line-height: 300%;
  padding-left: 1%;
  text-transform: uppercase;
}
.icon {
  padding-right: 1%;
}
.folderBody {
  border: 2px solid#116aab;
  background-color: white;
  width: 99.7%;
  height: 200px;
  /* padding-bottom: 1%; */
}

.noSelect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* **Scroll** */

/* width */
.folderBody::-webkit-scrollbar {
  width: 7px;
}

/* Track */
.folderBody::-webkit-scrollbar-track {
}

/* Handle */
.folderBody::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

/* Handle on hover */
.folderBody::-webkit-scrollbar-thumb:hover {
  background: #555;
}
