.dropDown {
  position: fixed;
  background-color: #116aab;
  width: 100px;
  height: 22px;
  /* border: 1.5px solid white; */
  border-radius: 7px;
  top: 70px;
  /* right: 22.5%; */
  z-index: 10;
}
.btnText {
  color: white;
  text-align: center;
  font-size: 60%;
  margin-top: 4%;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 600;
}

.icon {
  margin-left: 5%;
}

.dropDownContent {
  color: white;
  display: none;
  position: fixed;
  background-color: #116aab;
  width: 60px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 12px 10px;
  /* border: 1px solid rgba(0, 124, 168, 0.349); */
  width: 70px;
  border-radius: 0px 0px 10px 10px;
  z-index: 12;
  font-size: 45%;
  margin-top: 0.28%;
  margin-left: 0.5%;
}

.list {
  margin-top: 6%;
  /* background-color: red; */
}

.list:hover {
  cursor: pointer;
  background-color: rgba(0, 124, 168, 0.5);
  color: white;
  font-weight: 700;
}

.dropDown:hover .dropDownContent {
  display: block;
}
