.row {
  zoom: 1;
  max-width: 1140px;
  margin: 0;
}

.row:before,
.row:after {
  content: "";
  display: table;
}
.row:after {
  clear: both;
}

.col {
  display: block;
  float: left;
  margin: 1% 0 1% 0;
}

.col:first-child {
  margin-left: 0;
}
.span1of2 {
  width: 60%;
  /* background-color: burlywood; */
}
.span2of2 {
  width: 40%;
  /* background-color: red; */
}

@media only screen and (max-width: 480px) {
  .col {
    margin: 0;
  }

  /* .span3of3 {
		width: 100%;
	} */
}

/*  COMPONENT SETUP   ==================================================================== */

.heading {
  font-size: 80%;
}
