.row {
  zoom: 1;
  max-width: 1140px;
  margin: 0;
}

.row:before,
.row:after {
  content: "";
  display: table;
}
.row:after {
  clear: both;
}

.col {
  display: block;
  float: left;
  margin: 1% 0 1% 0;
}

.col:first-child {
  margin-left: 0;
}
.span1of2 {
  width: 60%;
  /* background-color: burlywood; */
}
.span2of2 {
  width: 40%;
  /* background-color: red; */
}
.btnSpan1of2 {
  width: 3%;
  /* background-color: red; */
}
.btnSpan2of2 {
  width: 97%;
  /* background-color: blue; */
}
/* .span3of3 {
	width: 33.31%;
	background-color: burlywood;
} */

@media only screen and (max-width: 480px) {
  .col {
    margin: 0;
  }

  /* .span3of3 {
		width: 100%;
	} */
}

/*  COMPONENT SETUP   ==================================================================== */

.sideBar {
  position: fixed;
  z-index: 10;
  height: 100%;
  flex-flow: column;
  vertical-align: middle;
  background-color: #202020;
  height: 100%;
  width: 300px;
  box-shadow: 2px 0 8px 2px rgba(0, 0, 0, 0.55);
  animation: mainDisplayLeft 0.25s;
  overflow: auto;
  background-color: white;
  color: black;
}

.sideBarOpen {
  right: 0;
  display: block;
  /* height: -webkit-fill-available; */
}
.sideBarClose {
  display: none;
}
.panelBar {
  background-color: white;
}

.sideBarBtn {
  cursor: pointer;
  margin-top: 290px;
  background-color: #116aab;
  z-index: 12;
  color: white;
  width: 10px;
  height: 27px;
}
.sideBarBtnAbsolute {
  position: fixed;
  cursor: pointer;
  top: 45%;
  right: 0;
  background-color: #116aab;
  z-index: 12;
  color: white;
  width: 10px;
  height: 27px;
}
.iconStyle {
  padding-left: 8%;
  padding-top: 80%;
  font-size: 40%;
}

