nav {
  position: fixed;
  background-color: #116aab;
  padding-left: 20px;
  z-index: 10;
  width: 100%;
}
.mainNav {
  margin-left: 1%;
}

.logo {
  padding-top: 12px;
  padding-bottom: 18px;
  height: 30px;
}

.heading {
  color: white;
  font-size: 80%;
  font-weight: 700;
  padding-top: 2.5%;
  text-align: left;
}

.noSelect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@media only screen and (max-width: 1200px) {
  .navList li {
    border: none;
    padding-right: 0;
  }
}

@media only screen and (max-width: 767px) {
  .navList li {
    width: 100%;
    margin-left: 10px;
  }
}
